.gameover {
  width: 100%;
  height: 100vh;
  background-color: rgb(250, 66, 66);
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  h1 {
    font-size: 26rem;
    color: rgb(249, 184, 252);
  }

  .over {
    margin-top: -7rem;
  }

  p {
    font-size: 5rem;
    color: rgb(248, 229, 243);
  }

  .reset {
    border-radius: 10%;
    width: 25%;
    height: 8%;
    font-size: 3.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    outline: none;
    margin: 3rem 0;
    background-color: rgb(245, 180, 253);
    text-decoration: none;
  }
}
